<script>
	import { currentTheme } from "../../store";
</script>

<div class="mobile-landing">
	<div class="content">
		<div class="icon">
			<i class="fas fa-desktop"></i>
		</div>
		<h1>Desktop Only</h1>
		<p class="subtitle">Questa applicazione  ottimizzata solo per desktop</p>
		
		<div class="message">
			<p>
				Il portfolio  stato progettato per offrire la migliore esperienza su schermi desktop.
			</p>
			<p>
				Per favore, accedi da un computer per esplorare tutti i progetti e le funzionalit interattive.
			</p>
			<p class="coming-soon">
				 La versione mobile verr rilasciata a breve!
			</p>
		</div>

		<div class="icon-row">
			<i class="fas fa-laptop"></i>
			<i class="fas fa-arrow-left"></i>
			<i class="fas fa-mobile-alt mobile-icon-crossed"></i>
		</div>

		<div class="footer">
			<p>Grazie per la comprensione!</p>
		</div>
	</div>
</div>

<style>
	.mobile-landing {
		min-height: 100vh;
		display: flex;
		justify-content: center;
		align-items: center;
		background: var(--bg-primary);
		background-image: var(--gradient);
		padding: 2rem;
		text-align: center;
	}

	.content {
		max-width: 600px;
		background: rgba(255, 255, 255, 0.1);
		backdrop-filter: blur(10px);
		border-radius: 20px;
		padding: 3rem 2rem;
		box-shadow: 0 10px 40px rgba(0, 0, 0, 0.3);
		border: 1px solid rgba(255, 255, 255, 0.2);
		animation: fadeInUp 0.6s ease-out;
	}

	.icon {
		font-size: 5rem;
		color: white;
		margin-bottom: 1.5rem;
		animation: pulse 2s ease-in-out infinite;
	}

	h1 {
		font-size: 2.5rem;
		font-weight: 700;
		color: white;
		margin: 0 0 0.5rem 0;
		text-shadow: 0 2px 10px rgba(0, 0, 0, 0.3);
	}

	.subtitle {
		font-size: 1.2rem;
		color: rgba(255, 255, 255, 0.9);
		margin: 0 0 2rem 0;
		font-weight: 300;
	}

	.message {
		background: rgba(0, 0, 0, 0.2);
		border-radius: 12px;
		padding: 1.5rem;
		margin: 2rem 0;
	}

	.message p {
		color: white;
		font-size: 1rem;
		line-height: 1.6;
		margin: 0.75rem 0;
	}

	.coming-soon {
		font-weight: 500;
		color: #48bb78 !important;
		margin-top: 1.5rem !important;
		padding-top: 1rem;
		border-top: 1px solid rgba(255, 255, 255, 0.1);
		font-size: 1.1rem !important;
	}

	.icon-row {
		display: flex;
		justify-content: center;
		align-items: center;
		gap: 2rem;
		font-size: 3rem;
		color: white;
		margin: 2rem 0;
		opacity: 0.8;
	}

	.mobile-icon-crossed {
		position: relative;
		opacity: 0.5;
	}

	.mobile-icon-crossed::after {
		content: '';
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%) rotate(45deg);
		width: 3px;
		height: 120%;
		background: #ff6b6b;
		box-shadow: 0 0 10px rgba(255, 107, 107, 0.5);
	}

	.footer {
		margin-top: 2rem;
		padding-top: 2rem;
		border-top: 1px solid rgba(255, 255, 255, 0.2);
	}

	.footer p {
		color: white;
		font-size: 1.1rem;
		margin: 0;
		font-weight: 300;
		opacity: 0.9;
	}

	@keyframes fadeInUp {
		from {
			opacity: 0;
			transform: translateY(30px);
		}
		to {
			opacity: 1;
			transform: translateY(0);
		}
	}

	@keyframes pulse {
		0%, 100% {
			transform: scale(1);
		}
		50% {
			transform: scale(1.1);
		}
	}

	/* Responsive per tablet */
	@media (max-width: 768px) {
		.content {
			padding: 2rem 1.5rem;
		}

		h1 {
			font-size: 2rem;
		}

		.subtitle {
			font-size: 1rem;
		}

		.icon {
			font-size: 4rem;
		}

		.icon-row {
			font-size: 2rem;
			gap: 1rem;
		}
	}

	@media (max-width: 480px) {
		.mobile-landing {
			padding: 1rem;
		}

		.content {
			padding: 2rem 1rem;
		}

		h1 {
			font-size: 1.5rem;
		}

		.icon {
			font-size: 3rem;
		}

		.message p {
			font-size: 0.9rem;
		}
	}
</style>
